@mixin transition ($one, $two, $three, $time,
  $function)
  {transition-property: $one $two $three;
   transition-duration: $time;
   transition-timing-function: $function;}

@mixin font($ff, $fs, $fw, $lh, $ls, $fc,)
{
  font-family: $ff;
  font-size: $fs;
  font-weight: $fw;
  line-height: $lh;
  letter-spacing: $ls;
  color: $fc;

}

@mixin flex($display, $ai, $jc, $direction, $wrap)
{ display: $display;
  align-items: $ai;
  justify-content: $jc;
  flex-direction: $direction;
  flex-wrap: $wrap;
}

@mixin list($l, $t, $q, $wrap)
{ display: flex;
  margin-left: -1 * $l;
  margin-top: -1 * $t;
  flex-wrap: $wrap;

}

@mixin listItem($l, $t, $gap,)
{margin-left: $l;
  margin-top: $t;
  flex-basis: calc(100% / #{$q} - #{$gap});

}

@mixin sectionMixin($t, $b,)
{padding-top: $t;
  padding-bottom: $b;

}


@mixin fontHeader($fw, $fs, $lh) {
  font-weight: $fw;
  font-size: $fs;
  line-height: $lh;
}